import { Link } from 'react-router-dom'
import styles from './BackBtn.module.scss'

function BackBtn() {
  return (
    <Link to="/react-sneakers/">
      <div className={`${styles.btn} mr-20 cu-p`}>
        <img src="img/btn-back.svg" alt="Button back" />
      </div>
    </Link>
  )
}

export default BackBtn
