import axios from 'axios'
import { useContext, useEffect, useState } from 'react'
import Card from '../components/Card'
import BackBtn from '../components/BackBtn/BackBtn'
import PageState from '../components/PageState'
import AppContext from '../context'

function Orders() {
  const { handlerAddToFavorite } = useContext(AppContext)
  const [orders, setOrders] = useState([])
  const [isLoading, setIsLoading] = useState(true)

  useEffect(() => {
    ;(async () => {
      try {
        const { data } = await axios.get(
          'https://6374c21648dfab73a4e7e504.mockapi.io/orders'
        )
        setOrders(data.reduce((prev, obj) => [...prev, ...obj.items], []))
        setIsLoading(false)
      } catch (error) {
        alert('Ошибка')
        console.error(error)
      }
    })()
  }, [])

  return (
    <div className="content p-40">
      <div className="d-flex align-center flex-start mb-40">
        <BackBtn />
        <h1>Мои заказы</h1>
      </div>

      <div className="d-flex flex-wrap justify-between">
        {orders.length > 0 ? (
          <>
            {(isLoading ? [...Array(4)] : orders).map((item, index) => (
              <Card key={index} {...item} isLoading={isLoading} />
            ))}
          </>
        ) : (
          <PageState
            title="У вас нет заказов"
            description="Оформите хотя бы один заказ"
            image={'img/emoji-sad.png'}
          />
        )}
      </div>
    </div>
  )
}

export default Orders
